import React from "react";
import image from "../../assets/img/yoga.jpg";
import { withRouter } from "react-router-dom";
function Yoga(props) {
  return (
    <div class="site-section" style={{ marginTop: "100px" }}>
      <div class="container">
        <div class="row">
          <div class="col-lg-6">
            <p class="mb-5">
              <img src={image} alt="Image" class="img-fluid" />
            </p>
          </div>
          <div class="col-lg-5 ml-auto">
            <h2 class="site-section-heading mb-3">YOGA</h2>
            <p>
              Praktikovanjem tehnika joge postie se vea emotivna
              uravnoteenost, odsustvo doivljaja uskraenosti i destrukcije,
              zadovoljstvo sobom, oseanje smisla ivota, poznavanje sopstvenog
              naina reagovanja  kroz usredsreivanje panje na deavanja u
              svom telu, usporavanje i produbljivanje disanja, sklad disanja i
              pokreta, telesnu i mentalnu relaksaciju. Na taj nain se podstie
              i okrenutost sadanjosti, utemeljnost u realnosti i perspektivno
              posmatranje budunosti. Hatha Yoga pogodna je za poetnike, isto
              kao i za napredne vebae, a preporuuje se svima kojima je
              potreban sklad i balans u ivotu.
            </p>
          </div>
        </div>
      </div>

      <div class="border-bottom">
        <div class="row no-gutters">
          <div class="col-md-12 col-lg-4">
            <div class="w-100 h-100 block-feature p-5 bg-light">
              <h2 class="underline">
                <span>Bakram joga</span>
              </h2>
              <p>
                Bikram joga poznatija kao vrela joga izvodi se u prostoriji
                zagrejanoj na 30 do 40 C. Prua blagi efekat saune, a vebe se
                izvode u serijama. Samo poetna serija ima 26 poloaja.
              </p>
              <p>
                <a
                  onClick={() => {
                    props.history.push("/bakram-joga");
                  }}
                  class="btn btn-outline-primary py-2 px-4"
                >
                  Sazanaj vie
                </a>
              </p>
            </div>
          </div>
          <div class="col-md-12 col-lg-4">
            <div class="w-100 h-100 block-feature p-5">
              <h2 class="underline">
                <span>Atanga joga</span>
              </h2>
              <p>
                Atanga/Vinjasa poznata je i kao joga snage koja se veba brzo
                i vezano, gotovo bez pauza, u nizu. Stvara toplotu u telu,
                proiava organizam kroz cirkulaciju i znojenje. Jedna od
                najpoznatijih serija ove vrste joge je Pozdrav suncu.
              </p>
              <p>
                <a
                  onClick={() => {
                    props.history.push("/astanga-joga");
                  }}
                  class="btn btn-outline-primary py-2 px-4"
                >
                  Sazanaj vie
                </a>
              </p>
            </div>
          </div>
          <div class="col-md-6 col-lg-4">
            <div class="w-100 h-100 block-feature p-5 bg-light">
              <h2 class="underline">
                <span>Bakiti joga</span>
              </h2>
              <p>
                Mnogi kau da je zapravo najtei oblik joge jer predstavlja punu
                posveenost. Radi se o traenju puta ka Bogu i potpunom
                posveenju. Izvodi se meditacijom, pevanjem mantri i duhovnih
                pesama.
              </p>
              <p>
                <a
                  onClick={() => {
                    props.history.push("/bakiti-joga");
                  }}
                  class="btn btn-outline-primary py-2 px-4"
                >
                  Sazanaj vie
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default withRouter(Yoga);
